import Vue from "vue";
import VueRouter from "vue-router";
import OktaVue,{LoginCallback} from '@okta/okta-vue';
import { OktaAuth } from '@okta/okta-auth-js';
import Home from "../views/Home.vue";
import Register from '../views/Register.vue'; 
import AI4K12 from '../views/AI4K12.vue'; 
import Repository from '../views/Repository.vue'; 
import About from '../views/About.vue'; 
import Tool from '../views/Tool.vue'; 
import rTool from '../views/rTool.vue'; 
import Mtool from '../views/mtool.vue'; 
import Contact from '../views/Contact.vue';
import Login from '../views/Login.vue'; 
import Menu from '../views/Menu.vue';  
import Users from '../views/Users.vue';  

//const callback_path = 'callback';

const config = {
  issuer: 'https://dev-94483832.okta.com',
  clientId:'0oa1ffu4idnXxDRnJ5d7',
  redirectUri: window.location.origin + '/login/callback',
  scopes: ['openid', 'profile']
  }

const oktaAuth = new OktaAuth(config);

Vue.use(VueRouter);
Vue.use(OktaVue, {oktaAuth});

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/login/callback",
    name: "Callback",
    component:LoginCallback
  },  
  {
    path: "/registo",
    name: "Registo",
    component: Register
  },
  {
    path: "/AI4K12",
    name: "AI4K12",
    component: AI4K12
  },
  {
    path: "/repositorio",
    name: "Repositorio",
    component: Repository
  },
  {
    path: "/about",
    name: "Acerca",
    component:About,
  },
  {
    path: "/tool",
    name: "Ver ferramentas",
    component:Tool,
  },
  {
    path: "/rtool",
    name: "Registar ferramenta",
    component:rTool
  },
  {
    path: "/mtools",
    name: "Menu feramentas",
    component: Mtool
  },
  {
    path: "/contato",
    name: "Contato",
    component:Contact
  },
    {
    path: "/menu",
    name: "Menu",
    component:Menu, meta: {requiresAuth:true}
  },
  {
    path: "/login",
    name: "Login",
    component:Login
  },
  {
    path: "/users",
    name: "Users",
    component:Users
  },  
   // Redirect to 404 page, if no match found
  {
    path: "*",
    component: () => import("@/views/Error404.vue"),
  },
]});

export default router;
